header {
  .container {
    .logo-wrapper {
    }
    nav {
      display: none;
      @media(min-width: 768px){
        display: block;
      }
      > ul {
        > li {
          display: inline-block;
          float: left;
          &.has-subnav {
            position: relative;
            .subnav {
              display: none;
              z-index: 1;
              &.show-subnav {
                display: block;
                position:absolute;
                top: 100%;
              }
              .subnav-item {
                background: @color-neutral;
              }
            }
          }
        }
      }
      &.mobile-menu {
        @media(max-width:767px){
          .display-flex();
					.flex-direction(column);
					.justify-content(center);
					.align-items(center);
					position: fixed;
          top: 0;
          bottom: 0;
          left: 0;
          right: 0;
          background-color: @color-secondary;
          z-index: 1;

        }
      }
    }
    .contact {
      display: none;
      @media(min-width: 768px){
        display: block;
      }
    }
    .burger-container {
      position: absolute;
      font-size: 50px;
      top: 10px;
      right: 10px;
      z-index: 1;
      @media(min-width: 768px){
        display: none;
      }
    }
  }
}
